import { Controller, TextPoint } from '@ali/4ever-cangjie';
import remove from '../commands/remove';

export default function onSelect(
  _: KeyboardEvent,
  controller: Controller,
  next: () => void,
) {
  const { value } = controller;
  const { selection, decorations, document } = value;
  const insertMention = decorations.find(
    (item) => item.mark.type === 'quick-insert',
  );

  if (!selection) {
    return next();
  }

  if (!selection.isCollapsed) {
    return next();
  }

  const start = selection.getStart(document) as TextPoint;
  const end = selection.getEnd(document) as TextPoint;

  if (insertMention) {
    if (
      start.offset > insertMention.end.offset ||
      end.offset < insertMention.start.offset
    ) {
      return controller.command(remove);
    }
  }
  return next();
}
